<style>
    /** WARN **/
    svg {
        padding: 20px 20px;
        width: 25vw;
        height: auto;
        max-width: 356px;
        min-width: 112px;
    }

    path {
        fill: white;
        opacity: 1;
    }

    label {
        position: absolute;
        /*float: left;*/
        top: 1em;
        left: 1em;
    }

    .centered {
        float: right;
        position: relative;
        margin: 10% 2% 0 0;
        padding: 2% 0 0 0;
        color: #676778;
        width: 66%;
        clear: none;
    }

    .centered span {
        font-family: 'TT Hoves Bold',sans-serif;
        font-size: 260%;
        font-weight: 600;
        letter-spacing: 0.12em;
        will-change: filter;
    }
</style>

<label>
    <input type="checkbox" bind:checked={logo_visible}>
    <!--<span>toggle me</span>-->
</label>

{#if logo_visible}

    <div class="centered" out:fly="{{y: 0, duration: 800}}">
        {#each title as char, i}
			<span
                    in:fade="{{delay: shape_render_duration + i * 150, duration: 800}}"
            >{char}</span>
        {/each}
    </div>

    <svg xmlns="http://www.w3.org/2000/svg"
         fill="none" width="136" height="136" viewBox="0 0 136 136" style="background-color:transparent;">
        <defs>
            <mask id="border-mask" x="0" y="0" width="308" height="136" maskUnits="userSpaceOnUse" maskContentUnits="userSpaceOnUse">
                <path fill="#fff" d="M0 0h308v136H0z"></path>
                <path d="M115.084 31.849L61.916.809a5.66 5.66 0 00-5.832 0L2.916 31.85C1.118 32.927 0 34.839 0 36.949v62.127c0 2.109 1.118 4.021 2.916 5.1l53.168 31.039c.923.54 1.895.785 2.916.785 1.02 0 2.041-.245 2.916-.785l53.168-31.039c1.798-1.03 2.916-2.991 2.916-5.1V36.948c0-2.108-1.118-4.02-2.916-5.1z" fill="#000"></path>
            </mask>
        </defs>

        {#if !hide_cori && !cori_rendered}
            <g out:fade="{{duration: text_render_duration}}" opacity=0.5>
                <path
                        in:expand="{{duration: text_render_duration, delay: shape_render_duration, easing: quintOut}}"
                        style="fill: #16343e; stroke: none;"
                        d={cori_text}
                />
                <path
                        in:draw="{{duration: shape_render_duration}}"
                        style="fill: none; stroke: #16343e; stroke-width: 1.5;"
                        d={cori_shape}
                />
                <path
                        in:expand="{{duration: text_render_duration, delay: shape_render_duration, easing: quintOut}}"
                        style="fill: #16343e; stroke: #16343e; stroke-width: 1.5;"
                        d={cori_shape}
                />
            </g>
        {:else if !hide_cori}
            <g out:fade="{{duration: text_render_duration}}" opacity=0.75>
                <path style="fill: #16343e; stroke: none;"
                      d={cori_text}
                />
                <path style="fill: #16343e; stroke:#16343e; stroke-width: 1.5;"
                      d={cori_shape}
                />
            </g>
        {:else if !hide_risi && !risi_rendered}
            <g out:fade="{{duration: text_render_duration}}" opacity=0.5 transform="scale(0.75, 0.76) translate(9, 12)">
                <path
                        in:expand="{{duration: text_render_duration, delay: shape_render_duration, easing: quintOut}}"
                        style="fill: #16343e; stroke: none;"
                        d={risi_text}
                />
                <path
                        in:draw="{{duration: shape_render_duration}}"
                        style="fill: none; stroke: #16343e; stroke-width: 1.5;"
                        d={risi_shape}
                />
                <path
                        in:expand="{{duration: text_render_duration, delay: shape_render_duration, easing: quintOut}}"
                        style="fill: #16343e; stroke: #16343e; stroke-width: 1.5;"
                        d={risi_shape}
                />
            </g>
        {:else if !hide_risi}
            <g out:fade="{{duration: text_render_duration}}" opacity=0.75 transform="scale(0.75, 0.76) translate(9, 12)">
                <path style="fill: #16343e; stroke: none;"
                        d={risi_text}
                />
                <path style="fill: #16343e; stroke:#16343e; stroke-width: 1.5;"
                        d={risi_shape}
                />
            </g>
        {/if}

    </svg>

{/if}

<link href="https://fonts.googleapis.com/css?family=Overpass:100,400" rel="stylesheet">

<script>
    import { onMount } from "svelte";
    import { quintOut } from 'svelte/easing';
    import { fade, draw, fly } from 'svelte/transition';
    import { expand } from './custom-transitions.js';
    // import { inner as shape, outer as text } from './shape.js';
    import { shape as cori_shape, text as cori_text } from './cori-shape.js';
    import { shape as risi_shape, text as risi_text } from './risi-shape.js';

    export let title = 'Mapping & Data Analytics';

    let logo_visible = false;
    let hide_cori = false;
    let cori_rendered = false;
    let hide_risi = true;
    let risi_rendered = false;
    let shape_render_duration = 1500;
    let text_render_duration = 500;
    let display_duration = 2500;

    let redraw = function (vis) {
        if (!!vis && !hide_cori && !cori_rendered) {

            logo_visible = true;

            setTimeout(
                () => {
                    cori_rendered = true;
                    risi_rendered = false;

                    setTimeout(
                        () => {
                            //console.log("hide cori");
                            hide_cori = !hide_cori;
                        },
                        display_duration
                    );

                    setTimeout(
                        () => {
                            //console.log("show risi");
                            hide_risi = !hide_risi;


                            setTimeout(
                                () => {
                                    risi_rendered = true;
                                    cori_rendered = false;

                                    // Restart
                                    if (!!logo_visible) setTimeout(
                                        () => {
                                            //console.log("show cori");
                                            hide_cori = !hide_cori;

                                            //console.log("hide risi");
                                            hide_risi = !hide_risi;

                                            //console.log("redraw")
                                            redraw(vis);
                                        },
                                        display_duration
                                    );
                                },
                                shape_render_duration + text_render_duration
                            );

                        },
                        display_duration - text_render_duration
                    );
                },
                shape_render_duration + text_render_duration
            );

            return true;

        } else if (!logo_visible) {
            cori_rendered = false;
            risi_rendered = false;
            return false;
        }
    };

    $: redraw(logo_visible)

    document.addEventListener("sidebar", function (evt) {
        //console.log("sidebar event dispatched");

        if ('target' in evt &&  !!evt.target.data['path']) {
            if (evt.target.data['path'].match(/about/) !== null) {
                logo_visible = true;
            }
        }
    }, true);
</script>
