<style>
    /** WARN **/

    nav {
        position: fixed;
        color: #1a1a1a;
        display: block;
        top: 0;
        right: 0;
        height: 100%;
        padding: 2rem 2rem 0.6rem 2rem;
        border-left: 1px solid #aaa;
        background: #fff;
        overflow-y: auto;
        width: 75vw;
        z-index: 1;
    }

    nav.hide-menu {
        display: block;
        transform: translateX(200%);
        -webkit-transition: -webkit-transform 0.5s;
        -moz-transition: -moz-transform 0.5s;
        transition: transform 0.5s;
    }

    nav.shown {
        display: block;
        transform: translateX(0);
        -webkit-transition: -webkit-transform 0.5s;
        -moz-transition: -moz-transform 0.5s;
        transition: transform 0.5s;
    }

    button.about {
        position: absolute;
        left: 2rem;
    }

    button.collapse-sidebar {
        position: absolute;
        right: 2rem;
    }
</style>

<nav class:shown="{!!show}" class:hide-menu="{!show}"
     on:sidebar={e => toggleSidebar(e)}
     transition:fly={{x: 250, opacity: 1}}>

    <button class="collapse-sidebar" on:click={() => show = !show}>Close Sidebar</button>

    <button class="about" on:click={e => { e.target.data={ action: "show", set: false, path: "about" }; return e.target.dispatchEvent(new CustomEvent("sidebar", e)) }}>About</button>

    <br /><br /><br />

    <div  class="sidebar-content">
        {#if (innerElms == null)}
            <slot />
        {:else}
            {@html innerElms.innerHTML}
        {/if}
    </div>

</nav>

<Modal bind:show={modal_show}>
    <div bind:this={modalElms}></div>
</Modal>

<script>
    import {onMount} from 'svelte';
    import { fly } from 'svelte/transition';
    import Modal from '../modals/Modal.svelte';
    import { ensure_custom_event_exists } from '../modules/polyfills';

    ensure_custom_event_exists();

    export let innerElms = null;
    export let show = false;
    let modal_show = false;
    let modalElms = null;

    let sinceKeyEvent = 0;

    let mounted = false;
    const processedElms = {};

    $: (function extractModalContentFromHTML (childElms, targetElms) {
        if (!!mounted && childElms != null && !!childElms.innerHTML) {
            // console.log("Before modal content filter:", childElms.childNodes.length, childElms.innerHTML);
            const childNodes = [ ...childElms.childNodes ]; // shallow copy

            for (const elm of childNodes) {
                if (elm.nodeType === 1 && elm.className.match(/modal-content/) !== null) {
                    const elm_id = ('id' in elm && !!elm.id) ? elm.id : elm.innerHTML.toString().substring(0, 16) + elm.toString();
                    const copy_elm = elm;

                    // console.log("Remove from inner: ", elm);
                    childElms.removeChild(elm);

                    if (elm_id in processedElms) {
                        // console.log("Already processed", elm_id);
                    } else {
                        // console.log("Add to modal:", copy_elm.innerHTML.toString());
                        targetElms.append(copy_elm);
                    }
                    processedElms[elm_id] = copy_elm;

                }
            }

            innerElms = childElms;
            // console.log("After modal content filter:", childElms.childNodes.length);
        }
    })(innerElms, modalElms);

    function toggleSidebar (evt) {
        if ('data' in evt.target &&
            'set' in evt.target.data &&
            'action' in evt.target.data &&
            evt.target.data['action'] === "show") {
            // console.log("toggleSidebar", evt.target.data);
            show = evt.target.data['set'];
        } else {
            show = !show;
        }

        if (!!show) {
            document.getElementById('sidebar').style.visibility = "visible";
        } else {
            document.getElementById('sidebar').style.visibility = "hide-menu";
        }
    }

    function showAbout (evt) {
        console.log("showAbout", evt.target.data);
        modal_show = true;
        show = false;
    }

    onMount(() => {
        if ((innerElms != null)) {
            // console.log("Sidebar inner:", innerElms.innerHTML);
        }

        mounted = true;

        document.addEventListener("sidebar", function (evt) {
            // console.log("sidebar event dispatched");

            if ('target' in evt &&  !!evt.target.data['path']) {
                if (evt.target.data['path'].match(/about/) !== null) {
                    showAbout(evt);
                }

            } else {
                toggleSidebar(evt);
            }
        }, true);

        document.addEventListener('keydown', function (evt) {
            const kbEvent = (evt || window['event']); // cross-browser shenanigans
            // console.log(kbEvent);

            if (kbEvent['keyCode'] === 32) { // spacebar
                kbEvent.preventDefault();

            } else if (kbEvent['ctrlKey'] || kbEvent['shiftKey']) {
                if (((new Date()).getTime() - sinceKeyEvent) > 66) {
                    if (kbEvent['keyCode'] === 73) {// I/i
                        sinceKeyEvent = (new Date()).getTime();
                        toggleSidebar(kbEvent);
                        kbEvent.preventDefault();
                    }
                }

            } else {
                // console.log('Keyboard Event: ', kbEvent['keyCode']);
            }
            return true;
        });
    });
</script>
