<script>
    import {onMount} from "svelte";
    import { quintOut } from 'svelte/easing';
    import { fade, draw, fly } from 'svelte/transition';
    import { expand } from '../logos/custom-transitions.js';
    import { shape as cori_shape, text as cori_text } from '../logos/cori-shape.js';
    import { shape as risi_shape, text as risi_text } from '../logos/risi-shape.js';

    export let light_on_dark = false;
    let sidebar_show = false;

    export let sidebarFunction = function (evt) {
        console.log("showSidebar", evt);

        sidebar_show = !sidebar_show;

        evt.target.data = {
            action: "show",
            set: sidebar_show,
            path: ('target' in evt && 'hash' in evt.target ) ?
                evt.target.hash :
                ""
        };
        evt.target.dispatchEvent(new CustomEvent("sidebar", evt));
    };

    let logo_visible = false;
    let hide_cori = false;
    let cori_rendered = false;
    let hide_risi = true;
    let risi_rendered = false;
    let shape_render_duration = 1500;
    let text_render_duration = 500;
    let display_duration = 2500;

    let redraw = function (vis) {
        if (!!vis && !hide_cori && !cori_rendered) {

            logo_visible = true;

            setTimeout(
                () => {
                    cori_rendered = true;
                    risi_rendered = false;

                    setTimeout(
                        () => {
                            // console.log("hide cori");
                            hide_cori = !hide_cori;
                        },
                        display_duration
                    );

                    setTimeout(
                        () => {
                            // console.log("show risi");
                            hide_risi = !hide_risi;


                            setTimeout(
                                () => {
                                    risi_rendered = true;
                                    cori_rendered = false;

                                    // Restart
                                    if (!!logo_visible) setTimeout(
                                        () => {
                                            // console.log("show cori");
                                            hide_cori = !hide_cori;

                                            // console.log("hide risi");
                                            hide_risi = !hide_risi;

                                            // console.log("redraw");
                                            redraw(vis);
                                        },
                                        display_duration
                                    );
                                },
                                shape_render_duration + text_render_duration
                            );

                        },
                        display_duration - text_render_duration
                    );
                },
                shape_render_duration + text_render_duration
            );

            return true;

        } else if (!logo_visible) {
            cori_rendered = false;
            risi_rendered = false;
            return false;
        }
    };

    $: redraw(logo_visible)

    onMount(function () {
        console.log("light_on_dark", light_on_dark);

        // console.log((this || window).document);

        const body = (this || window).document.body;
        // console.log(body);

        const header = (this || window).document.getElementById("desktop-header");
        // console.log(header);

        body.style.maxHeight = "348px";
        // body.style.paddingTop = header.offsetHeight + "px";
        // header.style.top = "-" + body.style.paddingTop + "px";

        logo_visible = true;
    });
</script>
