<style>
    button.sidebar-toggle {
        position: fixed;
        bottom: 0;
        left: 0;
        right: auto;
        word-break: keep-all;
    }
</style>

<Header light_on_dark="{(attributes !== null && 'light_on_dark' in attributes)? (!!attributes['light_on_dark'] && attributes['light_on_dark'] !== 'false') : false}" sidebarFunction="{showSidebar}"/>

<div bind:this={appsElms}>
    {#if (appsElms == null)}
        <slot />
    {/if}
</div>

{#if ((attributes !== null && 'show_sidebar_toggle_button' in attributes) && (!!attributes['show_sidebar_toggle_button'] && attributes['show_sidebar_toggle_button'] !== 'false'))}
    <button class="sidebar-toggle" on:click={e => showSidebar(e)}>{toggle_sidebar_label}</button>
{/if}

<script>
    import {onMount} from "svelte";
    import Header from "./headers/CORI_RISI_Header.svelte";

    export let attributes = null;
    export let innerElms = null;
    let appsElms = null;
    let sidebar_show = false;
    export let toggle_sidebar_label = "Toggle Sidebar";

    function showSidebar(evt) {
        console.log("showSidebar", evt);

        sidebar_show = !sidebar_show;

        evt.target.data = {
            action: "show",
            set: sidebar_show,
            path: ('target' in evt && 'hash' in evt.target ) ?
                evt.target.hash :
                ""
        };
        evt.target.dispatchEvent(new CustomEvent("sidebar", evt));
    }

    onMount(() => {
        // console.log("attributes", attributes);

        if (innerElms != null) {
            console.log("Apps inner:", innerElms.childNodes.length, innerElms.innerHTML);
            const childNodes = [ ...innerElms.childNodes ]; // shallow copy
            for (const elm of childNodes) {
                console.log(elm.nodeType, elm);
                if (elm.nodeType === 1) {
                    innerElms.removeChild(elm);
                    appsElms.append(elm);
                }
            }
        }
    });
</script>
