<style>
    /** WARN **/
    .modal-frame {
        display: block;
        pointer-events: none;
        visibility: hidden;
    }

    .modal-frame.shown {
        pointer-events: visible;
        visibility: visible;
    }

    .modal-overlay {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.5);
        z-index: 10;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .modal-container {
        position: relative;
        background-color: transparent;
        top: 1vh;
        left: 5vw;
        width: 90vw;
        height: 90vh;
        max-height: 620px;
        overflow: hidden;
    }

    main {
        /*max-height: calc(80vh - 10px);*/
        height: 100%;
        padding: 0.5rem;
        overflow: auto;
    }
</style>

<div class:shown="{!!show}" class="modal-frame">
    <div class="modal-overlay" data-close on:click={closeModal} transition:fade={{duration: 150}}>
        <div class="modal-container">
            <main>
                <!--{#if (!!innerHTML)}-->
                <!--    {@html innerHTML}-->
                <!--{:else}-->
                    <slot>
                    </slot>
                <!--{/if}-->
            </main>
        </div>
    </div>
</div>

<script>
    import { fade } from 'svelte/transition';
    import { onMount } from 'svelte';
    // import Toc from 'svelte-toc';

    // export let innerHTML = "";
    export let show = false;
    function closeModal(e) {
        if ('close' in e.target.dataset)
            show = false;
    }

    let sinceLastMovementEvent = 0;

    onMount(() => {

        document.addEventListener('keydown', function (evt) {
            const kbEvent = (evt || window['event']); // cross-browser shenanigans
            console.log(kbEvent);

            // if (kbEvent['ctrlKey'] || kbEvent['shiftKey']) {
            if (((new Date()).getTime() - sinceLastMovementEvent) > 66) {
                if (kbEvent['keyCode'] === 27) {// ESC
                    sinceLastMovementEvent = (new Date()).getTime();
                    kbEvent.target.dataset['close'] = true;
                    closeModal(kbEvent);
                    kbEvent.preventDefault();
                }
            }
            // }
            return true;
        });
    });
</script>
